
@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --gray: #b8a4b4;
}
* {
  font-family: "Open Sans", sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body , html {
  overflow-x: hidden;
  position: relative;
}
.gray {
  color: var(--gray);
}
.blue {
  color: #0095F6;
}
.instawritten {
  height: 54px;
  width: 177px;
  margin-bottom: 50px;
}

.full-height {
  height: calc(100vh - 120px);
}
.fb {
  background-color: #0095f6;
  color: white;
  padding: 10px 10px;
  display: flex;
  justify-content: center;
  width: 100%;
  border-radius: 9px;
  font-weight: 600;
  gap: 10px;
  font-size: 13px;
}
.fb svg {
  font-size: 17px;
}
.or {
  text-align: center;
  position: relative;
  width: 100%;
  display: block;
  margin: 25px 0;
  font-weight: 600;
  font-size: 13px;
}
.or::after {
  content: "";
  position: absolute;
  height: 1px;
  width: 40%;
  background-color: var(--gray);
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}
.or::before {
  content: "";
  position: absolute;
  height: 1px;
  width: 40%;
  background-color: var(--gray);
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

.input-box {
  position: relative;
}
.input-box input {
  width: 100%;
  height: 44px;
  border: 2px solid #f2f4f8;
  color: black;
  outline: none;
  background-color: #fafafa;
  color: black;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 12px;

  font-size: 12px;
  font-weight: 500;
  margin: 4px 0;
}
.input-box label {
  width: 100%;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray);
  font-size: 12px;
  transition: all 250ms ease-out;
  position: absolute;
  user-select: none;
  pointer-events: none;
}
.input-box input:valid ~ label {
  left: 10px;
  transform: unset;
  top: 6px;
  font-size: 10px;
}
.input-box input:focus ~ label {
    left: 10px;
    transform: unset;
    top: 6px;
    font-size: 10px;
  }
